
@import "../../App.css";

.Nav{
    
    display:flex;
    align-self: center;
    justify-content: space-between;
    width:100vw;
    position: absolute;
    z-index: 100;
}

.Logo {
    align-self: center;
    font-size:2em;
    font-weight:bold;
    padding:1em;
}

@media (min-width: 900px){
    .LinksContainer{
        align-self: center;

            font-size:1.5em;
           
                    
    }
    .LinksContainer ul{
    
            display: flex;
            list-style-type: none;
            padding-inline: 30px;
            color: var(--gray);
    }
    .LinksContainer li{
    padding-inline:30px;
       
    
    }

}

@media (max-width:900px){
    

    .LinksContainer{
        visibility: hidden;
        background-color:var(--gray);
        color:rgb(221, 221, 221);
        position:fixed;
        width:100vw;
        height: 100vh;
        font-weight:900;
        font-size:2em;
        padding:1em;

}
.LinksContainer ul{
    
    display: flex;
    flex-direction: column;
    list-style-type: none;
}
.LinksContainer li{
padding-top:30px;


}




}

  