@import "../../App.css";


.Hero{
    display:flex;

 flex-direction: column;
 align-items:center;   
    align-self: center;
    width:100vw;

padding-top: 50px;
padding-bottom: 250px;
background-size: 300% 300%;
background-image: linear-gradient(
          -45deg, 
          #ffb88e 0%, 
          rgb(255, 161, 73) 25%, 
          rgb(228, 199, 167) 51%, 
          #ffb170 100%
    );  
    animation: AnimateBG 20s ease infinite;


}

@keyframes AnimateBG { 
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
  }

.Hello{
    display:flex;
    margin-top: 0px;
    font-weight:900;
    font-size:3.5em;
    color:var(--gray);
}




.Name{
  padding-top:200px;
    color:var(--white);
    font-weight:900;
    font-size:5.5em;
}



.HeroContact{
  display:flex;
  justify-content: space-between;
  width: 15em;
  padding-top: 80px;
  margin-bottom:-30px;
  color:var(--gray);
    font-weight:400;
    font-size:1.5em;
    
}

@media (max-width:900px){
    .Hello{
        padding-top: 10px;
        font-weight:900;
        font-size:2.2em;
        color:var(--gray);;
    }
    
    .Name{
      color:var(--white);
      
        font-weight:900;
        font-size:3.4em;
    }
    
    .HeroContact{
        padding-top: 65px;
        margin-bottom: -80px;
        color:var(--gray);;
        font-weight:400;
        font-size:1.5em;
        width: 45%;
    }
    

}

.waves {

    width: 100%;
    height:15vh;
    margin-top: -15vh;
    min-height:100px;
    max-height:150px;
  }
  
  
  
  /* Animation */
  
  .parallax > use {
    animation: move-forever 25s cubic-bezier(.55,.5,.45,.5)     infinite;
  }
  .parallax > use:nth-child(1) {
    animation-delay: -12s;
    animation-duration: 30s;
  }
  .parallax > use:nth-child(2) {
    animation-delay: -13s;
    animation-duration: 70s;
  }
  .parallax > use:nth-child(3) {
    animation-delay: -14s;
    animation-duration: 90s;
  }
  .parallax > use:nth-child(4) {
    animation-delay: -15s;
    animation-duration: 120s;
  }
  @keyframes move-forever {
    0% {
     transform: translate3d(-90px,0,0);
    }
    100% { 
      transform: translate3d(85px,0,0);
    }
  }
  /*Shrinking for mobile*/
  @media (max-width: 768px) {
    .waves {
      height:90px;
      min-height:40px;
    }

  }